type=\"submit\"],\nhtml input[type=\"button\"],\nbutton {\n  cursor: pointer;\n  -webkit-appearance: button;\n\n  &[disabled] {\n    cursor: default;\n  }\n}\n\ntextarea {\n  vertical-align: top;\n  overflow: auto;\n}\n\ninput {\n  &[type=\"search\"] {\n    -webkit-appearance: textfield;\n    box-sizing: content-box;\n    -webkit-box-sizing: content-box;\n\n    &::-webkit-search-decoration,\n    &::-webkit-search-cancel-button {\n      -webkit-appearance: none;\n    }\n  }\n  &:-webkit-autofill {\n    text-shadow: none;\n  }\n}\n\n._md-visually-hidden {\n  border: 0;\n  clip: rect(0 0 0 0);\n  height: 1px;\n  margin: -1px;\n  overflow: hidden;\n  padding: 0;\n  position: absolute;\n  text-transform: none;\n  width: 1px;\n}\n\n.md-shadow {\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  border-radius: inherit;\n  pointer-events: none;\n}\n\n.md-shadow-bottom-z-1 {\n  @include md-shadow-bottom-z-1();\n}\n.md-shadow-bottom-z-2 {\n  @include md-shadow-bottom-z-2();\n}\n\n.md-shadow-animated.md-shadow {\n  transition: box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n/*\n * A container inside of a rippling element (eg a button),\n * which contains all of the individual ripples\n */\n.md-ripple-container {\n  pointer-events: none;\n  position: absolute;\n  overflow: hidden;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  transition: all 0.55s $swift-ease-out-timing-function;\n}\n\n.md-ripple {\n  position: absolute;\n  transform: translate(-50%, -50%) scale(0);\n  transform-origin: 50% 50%;\n  opacity: 0;\n  border-radius: 50%;\n  &.md-ripple-placed {\n    $sizeDuration: 0.45s * 2;\n    transition: margin $sizeDuration $swift-ease-out-timing-function,\n                border $sizeDuration $swift-ease-out-timing-function,\n                width $sizeDuration $swift-ease-out-timing-function,\n                height $sizeDuration $swift-ease-out-timing-function,\n                opacity $sizeDuration $swift-ease-out-timing-function,\n                transform $sizeDuration $swift-ease-out-timing-function;\n  }\n  &.md-ripple-scaled {\n    transform: translate(-50%, -50%) scale(1);\n  }\n  &.md-ripple-active, &.md-ripple-full, &.md-ripple-visible {\n    opacity: 0.20;\n  }\n}\n\n.md-padding {\n  padding: 8px;\n}\n\n.md-margin {\n  margin: 8px;\n}\n\n.md-scroll-mask {\n  position: absolute;\n  background-color: transparent;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: $z-index-scroll-mask;\n\n  > .md-scroll-mask-bar {\n    display: block;\n    position: absolute;\n    background-color: #fafafa;\n    right: 0;\n    top: 0;\n    bottom: 0;\n    z-index: $z-index-scroll-mask-bar;\n    box-shadow: inset 0px 0px 1px rgba(0, 0, 0, 0.3)\n  }\n}\n\n@media (min-width: $layout-breakpoint-sm) {\n  .md-padding {\n    padding: 16px;\n  }\n}\n\n// Bi-directional support\n\nhtml, body {\n  &[dir=rtl], &[dir=ltr] {\n     unicode-bidi: embed\n  }\n}\n\nbdo[dir=rtl] {\n  direction: rtl;\n  unicode-bidi: bidi-override;\n}\nbdo[dir=ltr] {\n  direction: ltr;\n  unicode-bidi: bidi-override;\n}\n\n\n// Global page styles\n//\n// [2] Ensure the page always fills at least the entire height of the viewport.\n// [3] Prevent iOS text size adjust after orientation change, without disabling user zoom\n// [4] Fonts on OSX will look more consistent with other systems that do not\n// render text using sub-pixel anti-aliasing.\n\nhtml, body {\n  -webkit-tap-highlight-color: rgba(0,0,0,0);\n  -webkit-touch-callout: none;\n\n  min-height: 100%; // [2]\n\n  -webkit-text-size-adjust: 100%; // [3]\n  -ms-text-size-adjust: 100%; // [3]\n\n  -webkit-font-smoothing: antialiased; // [4]\n  -moz-osx-font-smoothing: grayscale; // [4]\n}\n\nmd-select, md-card, md-list, md-toolbar,\nul, ol, p, h1, h2, h3, h4, h5, h6 {\n  //text-rendering: optimizeLegibility;\n}\n\n/************\n * Headings\n ************/\n.md-display-4 {\n  font-size: $display-4-font-size-base;\n  font-weight: 300;\n  letter-spacing: -0.010em;\n  line-height: $display-4-font-size-base;\n}\n.md-display-3 {\n  font-size: $display-3-font-size-base;\n  font-weight: 400;\n  letter-spacing: -0.005em;\n  line-height: $display-3-font-size-base;\n}\n.md-display-2 {\n  font-size: $display-2-font-size-base;\n  font-weight: 400;\n  line-height: rem(6.4);\n}\n.md-display-1 {\n  font-size: $display-1-font-size-base;\n  font-weight: 400;\n  line-height: rem(4);\n}\n.md-headline {\n  font-size: $headline-font-size-base;\n  font-weight: 400;\n  line-height: rem(3.2);\n}\n.md-title {\n  @include md-title();\n}\n.md-subhead {\n  @include md-subhead();\n}\n/************\n * Body Copy\n ************/\n.md-body-1 {\n  @include md-body-1();\n}\n.md-body-2 {\n  @include md-body-2();\n}\n.md-caption {\n  font-size: $caption-font-size-base;\n  letter-spacing: 0.020em;\n}\n.md-button {\n  letter-spacing: 0.010em;\n}\n\n/************\n * Defaults\n ************/\n\nbutton,\nselect,\nhtml,\ntextarea,\ninput {\n  font-family: $font-family;\n}\n\nselect,\nbutton,\ntextarea,\ninput {\n  font-size: 100%;\n}\n\n/*\n*\n*  Responsive attributes\n*\n*  References:\n*  1) https://scotch.io/tutorials/a-visual-guide-to-css3-flexbox-properties#flex\n*  2) https://css-tricks.com/almanac/properties/f/flex/\n*  3) https://css-tricks.com/snippets/css/a-guide-to-flexbox/\n*  4) https://github.com/philipwalton/flexbugs#3-min-height-on-a-flex-container-wont-apply-to-its-flex-items\n*  5) http://godban.com.ua/projects/flexgrid\n*\n*\n*/\n\n@mixin flex-order-for-name($sizes:null) {\n  @if $sizes == null {\n    $sizes : '';\n\n    .flex-order {\n     order : 0;\n    }\n  }\n\n  @for $i from -20 through 20 {\n    $order : '';\n    $suffix : '';\n\n    @each $s in $sizes {\n      @if $s != '' { $suffix : '#{$s}-#{$i}'; }\n      @else        { $suffix : '#{$i}';       }\n\n      $order : '.flex-order-#{$suffix}';\n    }\n\n    #{$order} {\n      order: #{$i};\n    }\n  }\n}\n\n@mixin offset-for-name($sizes:null) {\n  @if $sizes == null { $sizes : ''; }\n\n  @for $i from 0 through 19 {\n    $offsets : '';\n    $suffix : '';\n\n    @each $s in $sizes {\n      @if $s != '' { $suffix : '#{$s}-#{$i * 5}'; }\n      @else        { $suffix : '#{$i * 5}';       }\n\n      $offsets : '.offset-#{$suffix}, .flex-offset-#{$suffix}';\n    }\n\n    #{$offsets} {\n      @if $i != 0 { @include rtl-prop(margin-left, margin-right, #{$i * 5 + '%'}); }\n      @else { @include rtl-prop(margin-left, margin-right, 0); }\n    }\n  }\n\n  @each $i in 33 {\n    $offsets : '';\n    $suffix : '';\n\n    @each $s in $sizes {\n      @if $s != '' {  $suffix : '#{$s}-#{$i}';   }\n      @else        {  $suffix : '#{$i}';         }\n\n      $offsets : '.offset-#{$suffix}, .flex-offset-#{$suffix} ';\n    }\n\n    #{$offsets} {\n      margin-left: calc(100% / 3);\n    }\n  }\n\n  @each $i in 66 {\n    $offsets : '';\n    $suffix : '';\n\n    @each $s in $sizes {\n      @if $s != '' {  $suffix : '#{$s}-#{$i}';   }\n      @else        {  $suffix : '#{$i}';         }\n\n      $offsets : '.offset-#{$suffix}, .flex-offset-#{$suffix} ';\n    }\n\n    #{$offsets} {\n      @include rtl-prop(margin-left, margin-right, calc(200% / 3));\n    }\n  }\n}\n\n@mixin layout-for-name($name: null) {\n  @if $name == null { $name : '';          }\n  @if $name != ''   { $name : '-#{$name}'; }\n\n  .layout#{$name}, .layout#{$name}-column, .layout#{$name}-row {\n    box-sizing: border-box;\n    display: -webkit-box;\n    display: -webkit-flex;\n    display: -moz-box;\n    display: -ms-flexbox;\n    display: flex;\n  }\n  .layout#{$name}-column {  flex-direction: column;     }\n  .layout#{$name}-row    {  flex-direction: row;        }\n}\n\n@mixin flex-properties-for-name($name: null) {\n  $flexName: 'flex';\n  @if $name != null {\n    $flexName: 'flex-#{$name}';\n    $name : '-#{$name}';\n  } @else {\n    $name : '';\n  }\n\n  .#{$flexName}             { flex: 1;         box-sizing: border-box; }  // === flex: 1 1 0%;\n\n  .#{$flexName}-grow        { flex: 1 1 100%;  box-sizing: border-box; 